*{margin: 0; padding: 0; 
  box-sizing: border-box; 
  border-collapse: collapse;
}



.nav{
  width: 18%; height: 100vh;
  background: #ece8e7;
  position: fixed;
  top: 0; left: 0;
  padding: 50px 0 0 50px;
}

h1{ 
  width: 150px; 
  padding: 0 0 0 10px
}

.nav p{
  font-family: 'Cormorant', serif;
  padding: 5px 0;
  font-size: 2rem;
  color: rgb(57, 59, 42);
  font-weight: 800;
}

.nav p:hover{
  padding: 5px 0;
  font-size: 2rem;
  color: rgb(140, 179, 136);
  }


main { 
  width: 80%;
  margin-left: auto;
}


/*--------------Myfav------------*/

.tit{
  font-family: 'Cormorant', serif;  font-size: 2rem;
  padding-bottom: 70px;
}

.Myfav-wrap{
  width: 80%;
  margin-left: auto;
  text-align: center;
  padding: 100px 20px 50px;
}

.table-wrap{
  padding: 0 20px 20px;
}
table{
  width: 100%;
}

th{
  font-weight: 700;
  text-align: left;
  padding: 25px 0  25px 30px;
  border-bottom: 1px solid #999;
  background: none;
}

th:nth-of-type(1){
  padding: 0 0 0 45px;
}

td{
  width: 20%;
  text-align: left;
  padding: 10px 0  10px 30px;
}
td:nth-of-type(1){width: 2%;}
td:nth-of-type(2){width: 28%;}
td:nth-of-type(3){width: 15%;}
td:nth-of-type(4){width: 35%;}
td:nth-of-type(5){width: 3%;}
td:nth-of-type(6){width: 5%;}
td:nth-of-type(7){width: 2%;}

tr{
  border-radius: 25% 10%; 
  /* table border collapsÎêòÏñ¥ ÏûàÏúºÎ©¥ radiusÍ∞Ä ÏïàÎ®πÌûåÎåÄ */
}

tbody tr:nth-of-type(even){
  background: rgba(231, 229, 235, 0.3);
}

tbody tr:hover {
  background: rgba(197, 194, 204, 0.3);  
}
tbody tr:hover .btn_play{
  display: block;
}
.thumb{
  height: 100px;
  position: relative;
}

.forplay{
  position: relative;
  width: 100px;
  height: 72px;
}
.album-cover{
  width: 70px;
  border-radius: 5px;
  position: absolute;
  top: 0;
  left: 0;
}
.btn_play{
  display: none;
  width: 50px;
  position: absolute;
  top : 12% ; 
  left: 12% ;
}

.btn_see{
  width: 20px;
  margin: 0 20px 0 0;
  position: relative;
}
.btn_share{
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.btn_share a{
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.btn_share a:hover {background-color: #ddd;}

.show {display:block}




/* ------------------MusicVideo--------------------- */



.mv-test{
  width:  82%;
  margin-left: auto;
  padding: 100px 20px 50px;
  background: rgb(191, 226, 187);
  text-align: center;
}
.song-wrap{
  width: 800px;
  background:#999;
  margin: 0 auto;
  position: relative;
}

.song-wrap picture{
  position: absolute;
  top:0 ; 
  left: 0;
}

picture img{
  width: 100%;
}

.song-word{
  position: absolute;
  text-align: left;
  bottom:5% ; 
  left: 5% ;
}




/* ------------------------------------------------------ */

.bgm-test{
  background: royalblue;
  width:  82%;
  margin-left: auto;
  padding: 100px 20px 50px;
  text-align: center;
}

.bgm-comp{
  width: 400px; height: 400px;
  background: #d0d8e9;
  text-align: right;
}

/* --------------------play list------------------- */

.playlist {
  padding: 100px 0 50px 0;
  width: 50%;
  margin: 0 auto;
  /* border: 1px solid black; */
  
}

.playlist input{
  width: 80%;
  height: 30px;
  border: none;
  border-bottom: black 1px solid;
  margin: 0 20px 0 0;
}

.playlist button{
  width: 15%;
  height: 30px;
  background-color: black;
  color: white;
}
.playlist ul{
  padding: 50px 0;
  margin: 0 0 0 20px ;
}
.playlist li{
  padding: 10px 0;
  list-style-type:'üéß';
}

